import { Component, OnInit } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
import {List} from '../list.model';

@Component({
  selector: 'app-rsdonorthank',
  templateUrl: './rsdonorthank.component.html',
  styleUrls: ['./rsdonorthank.component.css']
})
export class RSdonorthankComponent implements OnInit {

  public response: any;
  public posts: List[] = [];


  constructor( public http: HttpClient) { }

  ngOnInit() {

    // Adding request data to db

    let body = new URLSearchParams();


    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.get("http://localhost:3000/blood/donor_type_count/", options).subscribe(responseData => {
      this.response = responseData;
      this.posts=this.response;
      console.log(this.response);
    });



  }



}
