import { Component, OnInit } from '@angular/core';
import { NgModel } from '@angular/forms';
import { VicholaService } from '../vichola.service';
import { Router } from '@angular/router';
import {HttpClient, HttpHeaders} from '@angular/common/http';

@Component({
  selector: 'app-donater',
  templateUrl: './donater.component.html',
  styleUrls: ['./donater.component.css']
})
export class DonaterComponent implements OnInit {

  public response: any;

  posts:[];

  name: string;
  Gender: string;
  Blood_group: string;
  how_often: string;
  Mobile_No: string;
  Address: string;
  eligibilty: boolean;



  constructor(private http: HttpClient, public route: Router) { }

  ngOnInit() {


    // check if already registered
    this.check_if_donor();


  }

  donaterData(form : NgModel){
    if (form.invalid){
      return;
    }
    this.name = form.value.humanName;
    this.Gender = form.value.gender;
    this.Blood_group = form.value.bloodGroup;
    this.how_often = form.value.bloodReg;
    this.Mobile_No = form.value.contactNumber;
    this.Address = form.value.homeAddress;
    this.eligibilty = form.value.eligibilty;
    // console.log(this.name,this.gender,this.bloodGroup,this.bloodReg,this.contact,this.address,this.eligibilty);
    // var donatorinfo={name:this.name,gender:this.gender,bloodGroup:this.bloodGroup,bloodReg:this.bloodReg,contact":"8126753642","address":"Dehradun","eli":"true"};
    
    //this.http.post(url,dontorinfo);

    // this.vichola.getDonaterData(this.name, this.gender, this.bloodGroup, this.bloodReg, this.contact, this.address, this.eligibilty);
    // form.reset();

    let body = new URLSearchParams();
    body.set('id_user', localStorage.getItem('id'));
    body.set('name', this.name);
    body.set('Gender', this.Gender);
    body.set('Blood_group', this.Blood_group);
    body.set('how_often', this.how_often);
    body.set('Mobile_No', this.Mobile_No);
    body.set('Address', this.Address);

    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.post("http://localhost:3000/blood/donor_registration/", body.toString(), options).subscribe(responseData => {
      this.response = responseData;
      console.log(this.response);
    });




    //
    this.route.navigate(['/RSdonorthank']);
  }


  check_if_donor() {

    let body = new URLSearchParams();
    body.set('id_user', localStorage.getItem('id'));

    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.post("http://localhost:3000/blood/check_if_donor/", body.toString(), options).subscribe(responseData => {
      this.response = responseData;
      console.log(this.response.is_a_donor);
      
      if (this.response.is_a_donor)
      {
        this.route.navigate(['/RSdonorthank']);


      }
      else {
        this.recentDonors();

      }
      
      
    });




    //


  }


  recentDonors() {


    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.get("http://localhost:3000/blood/donor_recent/", options).subscribe(responseData => {
      this.response = responseData;
      this.posts=this.response;
      console.log(this.response);
    });


  }


}
