import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {NgModel} from '@angular/forms';

@Component({
  selector: 'app-rslogin-signup',
  templateUrl: './rslogin-signup.component.html',
  styleUrls: ['./rslogin-signup.component.css']
})
export class RsloginSignupComponent implements OnInit {
  public response: any;

  name: string;
  email: string;
  password: string;

  RSname: string;
  RSemail: string;
  RSpassword: string;

  allow: Boolean = false;


  constructor(public route: Router, public http: HttpClient) {

  }

  ngOnInit() {
  }


  add(form: NgModel) {

    if (form.invalid){
      return;
    }


    this.email = form.value.email;
    this.password = form.value.pass;
    console.log(this.email, this.password);

    // form.reset();

    let body = new URLSearchParams();
    body.set('email', this.email);
    body.set('password', this.password);
    // body.set('dependency', 'God');
    // body.set('risk', 'God');
    // body.set('priority', 'God');
    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.post('http://localhost:3000/blood/login/', body.toString(), options).subscribe(responseData => {
      this.response = responseData;
      console.log(this.response.logged_in);

      if (this.response.logged_in) {

        console.log("Aage Jao");

        console.log(this.response.id);
        localStorage.setItem('id', this.response.id);


        this.route.navigate(['/Index']);


      }
      else {


        alert("Invalid Credentials")
        console.log("Invalid Credentials")

      }


    });

  }



  ///////////////////////////////////////////


  // @ts-ignore
  addR(form: NgModel) {

    if (form.invalid){
      return;
    }
    
     this.RSname = form.value.RSName;
     this.RSemail = form.value.RSemail;
     this.RSpassword = form.value.RSpass;

     
    // form.reset();

    let body = new URLSearchParams();
    body.set('email', this.RSemail);
    body.set('password', this.RSpassword);
    body.set('name', this.RSname);

    // body.set('dependency', 'God');
    // body.set('risk', 'God');
    // body.set('priority', 'God');
    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.post('http://localhost:3000/blood/register/', body.toString(), options).subscribe(responseData => {
      this.response = responseData;
      console.log(this.response.logged_in);

      if (this.response.registered) {

        //POPUP registered
        console.log("Aage Jao");
        // this.route.navigate(['/Index']);
        this.allow=true;


      }
      else {

        alert("Something went wrong!")
        // console.log("Invalid Credentials")

      }


    });

  }



}
