import {Component, OnInit} from '@angular/core';
import {VicholaService} from '../vichola.service';
import {NgModel} from '@angular/forms';
import {List} from '../list.model';
import {NavigationExtras, Router} from '@angular/router';
import {HttpClient, HttpHeaders} from '@angular/common/http';

@Component({
  selector: 'app-acceptor',
  templateUrl: './acceptor.component.html',
  styleUrls: ['./acceptor.component.css']
})
export class AcceptorComponent implements OnInit {

  public response: any;


  constructor(private http: HttpClient, public router: Router) {
  }


  ngOnInit() {
  }

  getAcceptorData(form: NgModel) {
    if (form.invalid) {
      return;
    }
    var gender= form.value.gender;//
    var Patient_Name= form.value.firstname;//
    var Blood_Group=  form.value.bloodGroup; //
    var Contact_Name= form.value.contactName;//
    var Contact_Number= form.value.phoneNumber;//
    var Hospital_Name_Address= form.value.hospAddr; //

    // Adding request data to db

    let body = new URLSearchParams();
    body.set('gender', gender);
    body.set('Patient_Name', Patient_Name);
    body.set('Blood_Group', Blood_Group);
    body.set('Contact_Name', Contact_Name);
    body.set('Contact_Number', Contact_Number);
    body.set('Hospital_Name_Address', Hospital_Name_Address);

    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.post("http://localhost:3000/blood/request_registration", body.toString(), options).subscribe(responseData => {
      this.response = responseData;
      console.log(this.response);
    });


    //

    console.log('In acceptor.component');

    // this.vichola.getBloodGroup(Blood_Group);

    let navigationExtras: NavigationExtras = {
      queryParams: {
        "Blood_Group": Blood_Group,
        "lastname": "Raboy"
      }
    };
    this.router.navigate(["/Result"], navigationExtras);
    //

    // this.router.navigate(['/Result']);
  }

}
