import { Component, OnInit } from '@angular/core';
import { VicholaService } from '../vichola.service';
import { List } from '../list.model';

import { Subject } from 'rxjs';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-results',
  templateUrl: './results.component.html',
  styleUrls: ['./results.component.css']
})
export class ResultsComponent implements OnInit {
  
  public posts: List[] = [];
  public response: any;
  public Blood_Group : string;



  constructor(public route: ActivatedRoute, public http: HttpClient) {



  }

  ngOnInit() {

    this.route.queryParams.subscribe(params => {
      this.Blood_Group = params["Blood_Group"];
      // this.lastname = params["lastname"];
    });


    // Adding request data to db

    let body = new URLSearchParams();
    body.set('Blood_Group', this.Blood_Group);

    console.log(this.Blood_Group);

    // body.set('password', 'password');
    // body.set('Mobile', 'password');

    let options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    this.http.post("http://localhost:3000/blood/donor_list_samegroup/", body.toString(), options).subscribe(responseData => {
      this.response = responseData;
      this.posts=this.response;
      console.log(this.response);
    });


    //



  }

}
